/* eslint-disable import/no-anonymous-default-export */
export default {
    namespace: 'cart',
    state: {
        shopCart: [],
        isEject: false,
    },
    effects: {
        // ‰ªéÊú¨Âú∞Â≠òÂÇ®‰∏≠Ëé∑ÂèñË¥≠Áâ©ËΩ¶Êï∞ÊçÆ
        *getShopCart(_, { call, put }) {
            let shopCart = yield JSON.parse(localStorage.getItem('shopCart'))
            yield put({
                type: 'setShopCart',
                payload: shopCart
            })
        }
    },
    reducers: {
        // Ê∑ªÂä†ÂïÜÂìÅËá≥Ë¥≠Áâ©ËΩ¶
        addToCart(state, {payload: good}) {
            // console.log('addToCart', good);
            let tempCart = state.shopCart
            let sign = false
            for (let v of tempCart) {
                if (v.id === good.id) {
                    v.quantity++
                    sign = true
                    break
                }
            }
            if (!sign) {
                good.quantity = 1
                tempCart.push(good)
            }
            state.shopCart = [...tempCart]
            if (!state.isEject) state.isEject = true
            return {
                ...state,
            }
        },
        // ÊâìÂºÄË¥≠Áâ©ËΩ¶
        openShop(state) {
            if (!state.isEject) state.isEject = true
            return { ...state }
        },
        // ÂÖ≥Èó≠Ë¥≠Áâ©ËΩ¶
        closeShop(state) {
            if (state.isEject) state.isEject = false
            return { ...state }
        },
        // Âà†Èô§Ë¥≠Áâ©ËΩ¶‰∏≠ÁöÑÂïÜÂìÅ
        deleteCart(state, {payload: id}) {
            // console.log('deleteCart', id);
            let tempCart = state.shopCart
            for (let i in tempCart) {
                if (tempCart[i].id === id) {
                    tempCart.splice(i, 1)
                    break
                }
            }
            if (tempCart.length === 0) state.isEject = false
            state.shopCart = tempCart.slice()
            return {
                ...state
            }
        },
        // Â¢ûÂä†‰∏Ä‰∏™ÂïÜÂìÅ
        addOneGood(state, {payload: id}) {
            let tempCart = state.shopCart
            for (let v of tempCart) {
                if (v.id === id) {
                    v.quantity++
                    break
                }
            }
            state.shopCart = tempCart.slice()
            return {
                ...state
            }
        },
        // ÂáèÂ∞ë‰∏Ä‰∏™ÂïÜÂìÅ
        subtrackGood(state, {payload: id}) {
            let tempCart = state.shopCart
            for (let v of tempCart) {
                if (v.id === id) {
                    v.quantity--
                    break
                }
            }
            state.shopCart = tempCart.slice()
            return {
                ...state
            }
        },
        // Ê∏ÖÁ©∫Ë¥≠Áâ©ËΩ¶
        clearShopCart(state) {
            state.shopCart = []
            state.isEject = false
            return {
                ...state
            }
        },
        // Ëé∑ÂèñÊú¨Âú∞Â≠òÂÇ®
        setShopCart(state, { payload: shopCart}) {
            return { ...state, shopCart }
        }
    }
}