import React, { useState } from 'react'
import './index.css'
import { Menu, Dropdown } from 'antd'
import { connect } from 'dva'
import 'antd/dist/antd.css';
import Items from '../Items/index'
import Spin from '../Spin/index'

const mapStateToProps = state => {
    return {
        data: state.products.data,
        sizes: state.products.sizes,
        error: state.products.error,
        isSpin: state.products.isSpin
    }
}

function Shelf(props) {
    // Â≠òÂÇ®ÂΩìÂâçÂ±ïÁ§∫ÁöÑ‰∏™Êï∞
    const [quantity, setQuantity] = useState(0)
    // Â≠òÂÇ®ÊéíÂ∫èÊñπÂºè
    const [sortWay, setSortWay] = useState('ÁÇπÂáªÈÄâÊã©')

    // ÁÇπÂáªÊéíÂ∫è
    const sortItem = (e) => {
        if (e.key === '0') {
            setSortWay('ÈªòËÆ§ÊéíÂ∫è')
        } else if (e.key === '1') {
            setSortWay('‰ª∑Ê†º‰ªé‰ΩéÂà∞È´ò')
        } else if (e.key === '2') {
            setSortWay('‰ª∑Ê†º‰ªéÈ´òÂà∞‰Ωé')
        }
        const { dispatch } = props
        dispatch({
            type: 'products/sortCarts',
            payload: {
                key: e.key
            }
        })
    }

    // ‰∏ãÊãâÁªÑ‰ª∂
    const menu = (
        <Menu onClick={(e) => sortItem(e)}>
            <Menu.Item key="0">ÈªòËÆ§ÊéíÂ∫è</Menu.Item>
            <Menu.Divider />
            <Menu.Item key="1">‰ª∑Ê†º‰ªé‰ΩéÂà∞È´ò</Menu.Item>
            <Menu.Divider />
            <Menu.Item key="2">‰ª∑Ê†º‰ªéÈ´òÂà∞‰Ωé</Menu.Item>
        </Menu>
    )

    const getNum = (len) => {
        setQuantity(len)
    }

    return (
        <div className="container">
            <div className="header">
                <span className="information">ÂÖ±Êü•ÊâæÂà∞ { quantity } ‰ª∂ÂïÜÂìÅ„ÄÇ</span>
                <div className="sort">
                    Ê†πÊçÆ&nbsp;&nbsp;
                    <Dropdown overlay={menu} trigger={['click']}>
                        <span className="select ant-dropdown-link" onClick={e => e.preventDefault()}> {sortWay} </span>
                    </Dropdown>
                    &nbsp;&nbsp;ÊéíÂ∫è
                </div>
            </div>
            <Items getNum={getNum} />
            <h1 style={{display: props.error === null ? 'none' : 'block', color: 'red'}}>Êï∞ÊçÆËé∑ÂèñÂ§±Ë¥•: {props.error && props.error.toString()}</h1>
            <Spin isSpin={props.isSpin} />
        </div>
    )
}

export default connect(mapStateToProps)(Shelf)
